const products = new Swiper('.products', {
  direction: 'horizontal',
  loop: true,
  navigation: {
    nextEl: '.product__control_next',
    prevEl: '.product__control_prev',
  },
  scrollbar: {
    el: '.swiper-scrollbar',
  },
  pagination: {
    el: '.swiper-pagination',
    clickable: true,
  },
  mousewheel: {
    sensitivity: 1,
  },
  slidesPerView: 1,
});

let sliderBrands = new Swiper('.slider-brands', {
  direction: 'horizontal',
  loop: true,

  navigation: {
    nextEl: '.slider-brands__control_next',
    prevEl: '.slider-brands__control_prev',
  },

  scrollbar: {
    el: '.swiper-scrollbar',
  },

  mousewheel: {
    sensitivity: 1,
  },

  breakpoints: {
    320: {
      slidesPerView: 2,
    },
    480: {
      slidesPerView: 4,
    },
    700: {
      slidesPerView: 6,
    },
    992: {
      slidesPerView: 8,
    }
  }
});

let sliderBestseller = new Swiper('.slider-bestseller', {
  direction: 'horizontal',
  loop: false,

  navigation: {
    nextEl: '.slider-bestseller__control_next',
    prevEl: '.slider-bestseller__control_prev',
  },

  scrollbar: {
    el: '.swiper-scrollbar',
  },

  mousewheel: {
    sensitivity: 1,
  },

  spaceBetween: 15,
  breakpoints: {
    320: {
      slidesPerView: 1,
    },
    543: {
      slidesPerView: 2,
    },
    821: {
      slidesPerView: 3,
    },
    1100: {
      slidesPerView: 4,
    }
  }

});